
body {
  margin: 0;
  padding: 0;
  background-image: url('./assets/bg_image.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* NavBar style */
.navbar {
  background-color: #050e1f;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 5px;
  display: flex;
  align-items: center;
}
.nav-links {
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-links a {
  color: #fac34d; /* Set the color to white or any other desired color */
  text-decoration: none; /* Remove underline */
}

.nav-links a:hover {
  /* Optionally, define styles for when the link is hovered */
  text-decoration: underline; /* Add underline on hover if desired */
}

.container {
  margin: 20px;
  /*
  margin-top: 20px;
  margin-left: 10px;
  margin-right: 10px;*/
  background-color: #050e1f;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.container p {
  color: #ffffff;
  font-size: 20px;
  margin: 0;
  padding: 0;
}
.container h1 {
  color: #fac34d;
  margin-top: 10;
  padding: 5;
}
